import { createStore } from 'vuex'

export default createStore({
  state: {
    theme: 'light'
  },
  getters: { 
    getTheme(state) {
      return state.theme
    }
  },
  mutations: {
    setTheme(state, payload) {
      return state.theme = payload
    }
  },
  actions: {
    addTheme(context, payload) {
      context.commit('setTheme', payload)
    }
  },
  modules: {
  }
})
