<template>
  <div id="nav">
    <div :class="`${light ? 'bg-gray-100' : 'bg-gray-900'} h-16 pt-3 px-3`">
      <div class="max-w-none mx-auto lg:max-w-7xl flex">
        <div class="flex w-1/3">
          <span>
            <img class="hidden sm:flex rounded flex items-center justify-center..." src="../../assets/img/rock.jpg" width="30" height="30"/>
          </span>
          <span class="font-semibold text-xl pl-3 pt-1 cursor-pointer"><router-link to="/" :class="`${light ? 'text-black' : 'text-white'}`">RoCkGonZ</router-link></span>
        </div>
        <div class="flex w-2/3 justify-end ... pt-2">
          <ul class="hidden md:flex">
            <li class="mr-12 pb-3">
              <router-link :class="`${pageURL === '/' ? 'text-blue-500' : 'text-gray-400'} hover:text-blue-800`" to="/">Home</router-link>
            </li>
            <li class="mr-12 pb-3">
              <router-link :class="`${pageURL === '/about' ? 'text-blue-500' : 'text-gray-400'} hover:text-blue-800`" to="/about">About</router-link>
            </li>
            <li class="mr-12 pb-3">
              <router-link :class="`${pageURL === '/skills' ? 'text-blue-500' : 'text-gray-400'} hover:text-blue-800`" to="/skills">Skills</router-link>
            </li>
            <li class="mr-12 pb-3">
              <router-link :class="`${pageURL === '/work' ? 'text-blue-500' : 'text-gray-400'} hover:text-blue-800`" to="/work">Work</router-link>
            </li>
            <li class="pb-3">
              <router-link :class="`${pageURL === '/contact' ? 'text-blue-500' : 'text-gray-400'} hover:text-blue-800`" to="/contact">Contact</router-link>
            </li>
            <li class="pl-6">
              <button @click="theme" class="focus:outline-none">
                <svg v-if="light" xmlns="http://www.w3.org/2000/svg" class="h-6 w-6 text-gray-400" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M12 3v1m0 16v1m9-9h-1M4 12H3m15.364 6.364l-.707-.707M6.343 6.343l-.707-.707m12.728 0l-.707.707M6.343 17.657l-.707.707M16 12a4 4 0 11-8 0 4 4 0 018 0z" />
                </svg>
                <svg v-else xmlns="http://www.w3.org/2000/svg" class="h-6 w-6 text-white" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M20.354 15.354A9 9 0 018.646 3.646 9.003 9.003 0 0012 21a9.003 9.003 0 008.354-5.646z" />
                </svg>
              </button>
            </li>
          </ul>
        </div>
        <div class="md:hidden pt-3">
          <button class="focus:outline-none" @click="toggleSidebar = !toggleSidebar">
            <svg xmlns="http://www.w3.org/2000/svg" :class="`${light ? 'text-black' : 'text-white'} h-6 w-6`" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M4 6h16M4 12h16M4 18h16" />
            </svg>
          </button>
        </div>
      </div>
    </div>
  </div>
  <div :class="`${light ? 'bg-blue-800' : 'bg-gray-900'} w-64 text-blue-100 min-h-screen absolute inset-y-0 left-0 transform ${toggleSidebar ? '-translate-x-full' : ''} transition duration-200 ease-in-out py-6 md:hidden`">
    <ul>
      <li @click="routes('/')" class="py-3 px-6 hover:bg-gray-700 cursor-pointer">Home</li>
      <li @click="routes('/about')" class="py-3 px-6 hover:bg-gray-700 cursor-pointer">About</li>
      <li @click="routes('/skills')" class="py-3 px-6 hover:bg-gray-700 cursor-pointer"><router-link to="/skills">Skills</router-link></li>
      <li @click="routes('/work')" class="py-3 px-6 hover:bg-gray-700 cursor-pointer"><router-link to="/work">Work</router-link></li>
      <li @click="routes('/contact')" class="py-3 px-6 hover:bg-gray-700 cursor-pointer"><router-link to="/contact">Contact</router-link></li>
      <li class="py-3 px-6">
        <button @click="theme" class="focus:outline-none">
          <svg v-if="light" xmlns="http://www.w3.org/2000/svg" class="h-6 w-6 text-white" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M12 3v1m0 16v1m9-9h-1M4 12H3m15.364 6.364l-.707-.707M6.343 6.343l-.707-.707m12.728 0l-.707.707M6.343 17.657l-.707.707M16 12a4 4 0 11-8 0 4 4 0 018 0z" />
          </svg>
          <svg v-else xmlns="http://www.w3.org/2000/svg" class="h-6 w-6 text-white" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M20.354 15.354A9 9 0 018.646 3.646 9.003 9.003 0 0012 21a9.003 9.003 0 008.354-5.646z" />
          </svg>
        </button>
      </li>
    </ul>
    <div class="absolute bottom-0 p-6 text-xs mt-12 text-gray-400">{{ views }} views</div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      toggleSidebar: true,
      light: true,
      views: 0
    }
  },
  computed: {
    pageURL() {
      return this.$route.path
    }
  },
  methods: {
    routes(params) {
      this.$router.push(params)
      this.toggleSidebar = true
    },
    theme() {
      this.light = !this.light
      const color = this.light ? 'light' : 'dark'
      this.$store.dispatch('addTheme', color)
      this.toggleSidebar = true
    }
  },
  created() {
      fetch("https://api.countapi.xyz/update/botikmot/rockgonz/?amount=1")
      .then(response => response.json())
      .then(response => {
        this.views = response.value
      })
  }
}
</script>